.login-box {
  width: 400px;
  margin: auto;
  padding: 40px;
  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}

.login-box h2 {
  margin-bottom: 30px;
  text-align: center;
  color: #333333;
}

.user-box {
  position: relative;
  margin-bottom: 25px;
}

.user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #333333;
  border: none;
  border-bottom: 1px solid #cccccc;
  outline: none;
  background: transparent;
}

.user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #999999;
  pointer-events: none;
  transition: 0.5s;
}

.user-box input:focus ~ label,
.user-box input:valid ~ label {
  top: -20px;
  font-size: 12px;
  color: #008374;
}

button[type="submit"] {
  width: 100%;
  background: #008374;
  border: none;
  outline: none;
  color: #ffffff;
  padding: 15px 0;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.5s ease;
}

button[type="submit"]:hover {
  background: #005a4d;
}

.button-span-container {
  display: flex;
  align-items: center;
}

.button-span {
  width: 10px;
  height: 10px;
  background: #ffffff;
  border-radius: 50%;
  margin: 0 2px;
  animation: button-animation 1s infinite;
}

@keyframes button-animation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
.button[type="submit"] {
  position: relative;
  width: 100%;
  background: #008374;
  border: none;
  outline: none;
  color: #ffffff;
  padding: 15px 0;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.5s ease;
  overflow: hidden; /* Ensure the animation stays within the button */
}

.button[type="submit"]:hover {
  background: #005a4d;
}

.button-animation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.8), transparent); /* Neon light gradient */
  animation: neon-circle 2s linear infinite; /* Adjust animation duration as needed */
}

@keyframes neon-circle {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
